// -----------------------------------------------------------
// General
//
// This file is meant for general styles and containers
// that apply everywhere throughout the website. Add more
// always repeating elements right below.
// -----------------------------------------------------------

// Correct Viewport Height on Mobile
// Source: https://css-tricks.com/the-trick-to-viewport-units-on-mobile/
:root {
  --vh: 1vh;
}

// Default structural element
.block {
  @include background-cover();
  background-color: #fff;
  position: relative;
  min-height: 100px;
  max-width: $layout-max-width;
  padding-left: $padding-default-cols;
  padding-right: $padding-default-cols;
  padding-top: $padding-default-rows;
  padding-bottom: $padding-default-rows;

  @media (max-width: $container-ipad) {
    padding-left: $padding-default-cols-ipad;
    padding-right: $padding-default-cols-ipad;
    padding-top: $padding-default-rows-ipad;
    padding-bottom: $padding-default-rows-ipad;
  }

  @media (max-width: $container-mobile) {
    padding-left: $padding-default-cols-mobile;
    padding-right: $padding-default-cols-mobile;
    padding-top: $padding-default-rows-mobile;
    padding-bottom: $padding-default-rows-mobile;
  }
}

// 404 stuff
.error404 .block.block-footer.site-footer {
  padding-top: 8rem;
}

// Default shade on top of blocks
.shade {
  background: #333;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 0;
  opacity: .15;
}

// Site wrapper
.site {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
}

// Containers
.container,
%container {
  margin: 0 auto;
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 0;
  padding-right: 0;
  max-width: $grid-base-size;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

// Clear block containers by default
.site-main .container {
  overflow: hidden;
  clear: both;
}

// Meta actions
.entry-footer {
  overflow: hidden;
  clear: both;
  display: block;
}

// Edit
.edit-link {
  text-align: left;
  width: 100%;
  display: block;
  clear: both;
  max-width: 100%;
  min-width: 0;
  margin-bottom: 0;
  margin-top: 0;
  top: 0;
  bottom: 0;
  padding: 0;
  overflow: hidden;

  a {
    clear: both;
    display: block;
    margin-top: 40px;
    padding: 0;
  }
}

// Global link helper
// This is useful in services -type of areas where the whole block is a link
// Remember to define aria-label or screen-reader-text for accessibility
a.global-link {
  width: 100%;
  height: 100%;
  z-index: 3;
  position: absolute;
  top: 0;
  left: 0;
}

