// Mint styles
body .block.block-mint {
  h2.block-title {
    color: $color-mint;
  }
}

.block-head {
  padding: 0;
}

.block-title-pre {
  @extend %h4;
  opacity: .5;
  margin-bottom: 2rem;
  margin-top: 0;
}

// Descriptions and notes
.col-description {
  opacity: .5;
}

// Positions
.is-centered {
  text-align: center;
}
